@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:300,regular,500,700,900&display=swap&subset=cyrillic-ext");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

a {
  text-decoration: none; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

html {
  font-size: 10px; }

@font-face {
  font-family: 'Uni Sans';
  src: local("../fontsUni Sans Heavy"), local("Uni-Sans-Heavy"), url("../fonts/UniSansHeavyCAPS.woff2") format("woff2"), url("../fonts/UniSansHeavyCAPS.woff") format("woff"), url("../fonts/UniSansHeavyCAPS.ttf") format("truetype");
  font-weight: 900;
  font-style: normal; }

@font-face {
  font-family: 'Play';
  src: url("../fonts/Play-Bold.eot");
  src: url("../fonts/Play-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Play-Bold.woff2") format("woff2"), url("../fonts/Play-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

._container {
  max-width: 1180px;
  margin: 0 auto;
  padding: 0 20px; }

.header {
  background: url("../img/Подложка2.jpg") top no-repeat; }

.top-header__content {
  display: flex;
  align-items: center;
  height: 90px; }

.top-header__column {
  flex: 0 1 50%; }
  .top-header__column_logo {
    align-self: flex-start;
    padding: 25px 0 0 0;
    flex: 0 0 190px;
    text-align: center; }

.menu__list {
  display: grid;
  grid-template-columns: 110px 130px 180px; }
  .menu__list li {
    margin: 1px 0; }

.menu__link {
  height: 27px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease 0s;
  padding: 5px 22px;
  font-family: 'Play';
  text-decoration: underline;
  color: #fff; }
  .menu__link:hover {
    border-radius: 20px;
    background-color: #058292; }

.contacts-header {
  display: flex;
  justify-content: center; }
  .contacts-header__column {
    padding: 0 30px;
    border-right: 1px solid #fff; }
    .contacts-header__column:last-child {
      border: 0; }
  .contacts-header__item_icon {
    background: url("../img/Иконка1.png") left no-repeat;
    padding: 0 0 0 37px; }
  .contacts-header__label {
    font-size: 12px;
    font-weight: 300;
    color: #fff;
    margin: 0 0 5px 0; }
  .contacts-header__phone {
    color: #fff;
    font-size: 18px;
    font-weight: 700; }

.bottom-header {
  display: flex; }
  .bottom-header_container {
    height: 63px;
    display: flex;
    align-items: center;
    justify-content: space-between; }
  .bottom-header__column {
    flex: 0 1 calc(50% - 95px); }

.actions-header {
  display: flex;
  align-items: center; }
  .actions-header li {
    padding: 0 54px 0 0; }
    @media (max-width: 1180px) {
      .actions-header li {
        padding: 0 4.57627vw 0 0; } }
  .actions-header__login {
    background: url("../img/Иконка2.png") left no-repeat;
    line-height: 24px;
    display: inline-block;
    padding: 0 0 0 36px;
    color: #999999; }
    .actions-header__login span {
      border-bottom: 1px dotted #000; }
  .actions-header__req {
    background: url("../img/Иконка при наведении.png") left no-repeat;
    line-height: 24px;
    display: inline-block;
    padding: 0 0 0 36px; }
    .actions-header__req span {
      border-bottom: 1px dotted  #1ab9ce;
      color: #1ab9ce; }
  .actions-header__email {
    background: url("../img/Иконка3.png") left no-repeat;
    line-height: 13px;
    display: inline-block;
    padding: 0 0 0 36px; }
    .actions-header__email span {
      border-bottom: 1px solid #1ab9ce;
      color: #1ab9ce; }

.info-header {
  display: flex;
  justify-content: center;
  align-items: center; }
  .info-header__column {
    padding: 0 54px 0 0; }
    @media (max-width: 1180px) {
      .info-header__column {
        padding: 0 4.57627vw 0 0; } }
    .info-header__column:last-child {
      padding: 0; }
  .info-header__callback {
    background: url("../img/Иконка4.png") left no-repeat;
    display: inline-block;
    color: #1ab9ce;
    padding: 0 0  0 32px;
    line-height: 20px; }
    .info-header__callback span {
      border-bottom: 1px dotted #1ab9ce; }
  .info-header__schedule {
    background: url("../img/Иконка5.png") left no-repeat;
    padding: 0 0 0 37px;
    font-size: 12px;
    color: #999999;
    font-weight: 300; }
    .info-header__schedule span {
      font-weight: 700; }
  .info-header__cart {
    background: url("../img/Иконка корзины.png") 0 0 no-repeat;
    width: 43px;
    height: 43px;
    display: inline-block;
    position: relative; }
    .info-header__cart span {
      position: absolute;
      padding: 0 11px;
      background-color: #f68038;
      border-radius: 20px;
      line-height: 25px;
      font-size: 18px;
      font-weight: 700;
      color: #fff;
      transform: translate(75%, 0px); }

body {
  font-family: 'Play-Bold', arial; }

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column; }
